body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --primaryColor: #09bfad;
  --accentColor: #e03268;
  --fixedWidth: 75%;
}

* ::-webkit-scrollbar {
  width: 5px;
  background: rgba(0, 0, 0, 0.1);
  box-shadow: inset 2px 2px 10px -1px rgba(0, 0, 0, 0.1);
}

* ::-webkit-scrollbar-thumb {
  background: #e03268 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Montserrat-Bold";
  src: local("Montserrat-Bold"), url(./public/fonts/Montserrat-Bold.ttf);
}

@font-face {
  font-family: "Montserrat-Light";
  src: local("Montserrat-Light"), url(./public/fonts/Montserrat-Light.ttf);
}

@font-face {
  font-family: "Montserrat-Regular";
  src: local("Montserrat-Regular"), url(./public/fonts/Montserrat-Regular.ttf);
}

@media screen and (max-width: 400px) {
  :root {
    --fixedWidth: 95%;
  }
}
